import barTitle from '@/components/zaiui-common/basics/bar-title';

import _my_cart_data from '@/static/zaiui/data/my_cart.js';	//虚拟数据
import _tool from '@/utils/tools.js';	//工具函数
export default {
	components: {
		barTitle,
	},
	data() {
		return {
			goodsList: [], checkAll: true, goods_checked: false,
		}
	},
	onLoad() {
		this.goodsList = _my_cart_data.goodsListData();
	},
	onReady() {
		_tool.setBarColor(true);
		uni.pageScrollTo({
		    scrollTop: 0,
		    duration: 0
		});
	},
	methods: {
		// 处理店铺全选-tongYue-2020-5-4
		checkGoods(i){
			if(!i.checked)
				i.goods.forEach(i=>{
					i.checked = false;
				})
			else
				i.goods.forEach(i=>{
					i.checked = true;
				})
		},
		// 处理全选-tongYue-2020-5-4
		checkAllDo(){
			//店铺处理
			let items = this.goodsList;
			
			if(this.checkAll)
				items.forEach(i=>{
					i.checked = false;
					//商品处理
					let goods = i.goods;
					goods.forEach(i=>{
						i.checked = false;
					})
				})
			else
				items.forEach(i=>{
					i.checked = true;
					//商品处理
					let goods = i.goods;
					goods.forEach(i=>{
						i.checked = true;
					})
				})
		},
		CheckboxChange(e) {
			let items = this.goodsList, values = e.detail.value;
			for (let i = 0; i < items.length; i++) {
				//店铺处理
				let result = values.includes(items[i].id + '');
				if (result) {
					items[i].checked = true;
				} else {
					items[i].checked = false;
				}
				//商品处理
				let goods = items[i].goods;
				for (let x = 0; x < goods.length; x++) {
					let result = values.includes(goods[x].id + '');
					if (result) {
						goods[x].checked = true;
					} else {
						goods[x].checked = false;
					}
				}
			}
		},
		tapChecked() {
			if (this.checkAll) {
				this.checkAll = false;
			} else {
				this.checkAll = true;
			}
		},
		barEditTap() {
			if (this.goods_checked) {
				this.goods_checked = false;
			} else {
				this.goods_checked = true;
			}
		},
		tapBtn() {
			uni.navigateTo({
				url: '/pages/goods/settlement'
			});
		}
	}
}
